"use client"

import * as React from "react"
import { usePathname, useRouter } from "next/navigation"

function extractLineIdFromPath(pathname, availableLineIds) {
  const segments = pathname?.split("/").filter(Boolean) ?? []
  if (segments.length === 0) return { lineId: null, hasLineIdInPath: false }
  const candidate = segments[0]
  if (Array.isArray(availableLineIds) && availableLineIds.includes(candidate)) {
    return { lineId: candidate, hasLineIdInPath: true }
  }
  return { lineId: null, hasLineIdInPath: false }
}

const LineContext = React.createContext(null)

export function LineProvider({ children, availableLineIds = [] }) {
  const pathname = usePathname()
  const router = useRouter()
  const fallbackLineId = React.useMemo(() => (availableLineIds.length > 0 ? availableLineIds[0] : null), [availableLineIds])
  const { lineId: derivedLineId, hasLineIdInPath } = React.useMemo(
    () => extractLineIdFromPath(pathname, availableLineIds),
    [pathname, availableLineIds]
  )

  const [lineId, setLineIdState] = React.useState(() => derivedLineId ?? fallbackLineId)

  React.useEffect(() => {
    if (hasLineIdInPath && derivedLineId && derivedLineId !== lineId) {
      setLineIdState(derivedLineId)
    }
  }, [derivedLineId, hasLineIdInPath, lineId])

  React.useEffect(() => {
    if (!lineId && fallbackLineId) {
      setLineIdState(fallbackLineId)
    }
  }, [fallbackLineId, lineId])

  const setLineId = React.useCallback(
    (nextLineId, { syncPath = true } = {}) => {
      if (!nextLineId) return
      setLineIdState(nextLineId)

      if (!syncPath) return

      const rawSegments = (pathname ?? "").split("/")
      if (rawSegments.length > 1) {
        const nextSegments = [...rawSegments]
        nextSegments[1] = nextLineId
        const nextPath = nextSegments.join("/") || "/"
        if (nextPath !== pathname) {
          router.push(nextPath)
        }
      }
    },
    [pathname, router]
  )

  const value = React.useMemo(() => ({ lineId, setLineId, availableLineIds }), [lineId, setLineId, availableLineIds])

  return <LineContext.Provider value={value}>{children}</LineContext.Provider>
}

export function useLine() {
  const context = React.useContext(LineContext)
  if (!context) {
    throw new Error("useLine must be used within a LineProvider")
  }
  return context
}
