"use client"

import * as React from "react"
import { ChevronsUpDown, Plus } from "lucide-react"

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuShortcut,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@/components/ui/sidebar"
import { useLine } from "@/providers/line-provider"

export function TeamSwitcher({
  teams = []
}) {
  const { isMobile } = useSidebar()
  const { lineId, setLineId } = useLine()
  const fallbackTeam = React.useMemo(() => teams?.[0] ?? null, [teams])
  const activeTeam = React.useMemo(() => {
    if (!Array.isArray(teams) || teams.length === 0) return null
    return teams.find((team) => team.lineId === lineId) ?? fallbackTeam
  }, [teams, lineId, fallbackTeam])

  React.useEffect(() => {
    if (!lineId && fallbackTeam) {
      setLineId(fallbackTeam.lineId, { syncPath: false })
    }
  }, [fallbackTeam, lineId, setLineId])

  const handleSelectTeam = React.useCallback(
    (team) => {
      setLineId(team.lineId)
    },
    [setLineId]
  )

  if (!activeTeam) {
    return null
  }

  return (
    <SidebarMenu>
      <SidebarMenuItem>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <SidebarMenuButton
              size="lg"
              className="data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground">
              <div
                className="bg-sidebar-primary text-sidebar-primary-foreground flex aspect-square size-8 items-center justify-center rounded-lg">
                <activeTeam.logo className="size-4" />
              </div>
              <div className="grid flex-1 text-left text-sm leading-tight">
                <span className="truncate font-medium">{activeTeam.name}</span>
                <span className="truncate text-xs">{activeTeam.plan}</span>
              </div>
              <ChevronsUpDown className="ml-auto" />
            </SidebarMenuButton>
          </DropdownMenuTrigger>
          <DropdownMenuContent
            className="w-(--radix-dropdown-menu-trigger-width) min-w-56 rounded-lg"
            align="start"
            side={isMobile ? "bottom" : "right"}
            sideOffset={4}>
            <DropdownMenuLabel className="text-muted-foreground text-xs">
              Teams
            </DropdownMenuLabel>
            {teams.map((team) => (
              <DropdownMenuItem
                key={team.lineId ?? team.name}
                onClick={() => handleSelectTeam(team)}
                className="gap-2 p-2 data-[active=true]:bg-muted"
                data-active={team.lineId === activeTeam?.lineId}
                aria-checked={team.lineId === activeTeam?.lineId}
              >
                <div className="flex size-6 items-center justify-center rounded-md border">
                  <team.logo className="size-3.5 shrink-0" />
                </div>
                <div className="flex flex-col">
                  <span className="text-sm font-medium leading-tight">{team.name}</span>
                  <span className="text-xs text-muted-foreground">{team.lineId}</span>
                </div>
              </DropdownMenuItem>
            ))}
            <DropdownMenuSeparator />
            <DropdownMenuItem className="gap-2 p-2">
              <div
                className="flex size-6 items-center justify-center rounded-md border bg-transparent">
                <Plus className="size-4" />
              </div>
              <div className="text-muted-foreground font-medium">Add team</div>
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </SidebarMenuItem>
    </SidebarMenu>
  );
}
